.container {
  @apply flex flex-col items-center py-8;
}
.title {
  @apply text-base font-normal text-center;
  @apply mb-4;
  color: #333333;
}

.extension {
  @apply inline-flex flex-row items-center;
  @apply py-1 pl-0 pr-1 rounded-md text-slate-600;
  background-color: #e9e7e4;
}
.extension > img {
  @apply w-4 h-4 mx-2;
}
.content {
  @apply pb-4 pt-4 mx-6 text-lg;
  color: #95a5b0;
}
.img-wrapper {
  position: relative;
  height: 200px;
  width: 100%;
  @apply m-auto mb-4;
}
.img-wrapper > img {
  height: 100%;
  @apply m-auto;
}
.content > p {
  text-indent: 2rem;
}
